
import { defineComponent } from "vue";
export default defineComponent({
  name: "Checkbox",
  props: {
    checked: {
      type: Boolean,
    },
  },
  emits: ["update:checked"],
  computed: {
    value: {
      get() {
        return this.checked;
      },
      set(value: boolean) {
        this.$emit("update:checked", value);
      },
    },
  },
});
